import logo from './logo.svg';
import './App.css';
import Particles from 'react-particles-js'
import { useSpring, animated } from 'react-spring'
import CenterBox from './Components/CenterBox'
import {useState} from 'react'
import Header from './Components/Header';

import PersonIcon from '@material-ui/icons/Person';
import EventAvailableIcon from '@material-ui/icons/EventAvailable';
import CodeIcon from '@material-ui/icons/Code';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import BusinessIcon from '@material-ui/icons/Business';
import WorkIcon from '@material-ui/icons/Work';
import AccountTreeIcon from '@material-ui/icons/AccountTree';
import GitHubIcon from '@material-ui/icons/GitHub';

const calc = (x, y) => [x - window.innerWidth / 2, y - window.innerHeight / 2]
const trans1 = (x, y) => `translate3d(${x / -10 + 500}px,${y / -10 + 350}px,0)`
const trans2 = (x, y) => `translate3d(${x / -10 + 500}px,${y / -10 - 50}px,0)`
const trans3 = (x, y) => `translate3d(${x / -10 - 500}px,${y / -10 - 50}px,0)`
const trans4 = (x, y) => `translate3d(${x / -10 - 500}px,${y / -10 + 350}px,0)`
const trans5 = (x, y) => `translate3d(${x / -10 + 500}px,${y / -10 + 150}px,0)`
const trans6 = (x, y) => `translate3d(${x / -10 + 500}px,${y / -10 - 250}px,0)`
const trans7 = (x, y) => `translate3d(${x / -10 - 500}px,${y / -10 + 150}px,0)`
const trans8 = (x, y) => `translate3d(${x / -10 - 500}px,${y / -10 - 250}px,0)`
const trans9 = (x, y) => `translate3d(${x / -10 - 0}px,${y / -10 + 50}px,0)`


function App() {

  const [props, set] = useSpring(() => ({ xy: [0, 0], config: { mass: 100, tension: 0, friction: -10 } })) 
  const [inBox, setInBox] = useState(false)
  const [showCenter, setShow] = useState(false)
  const [boxSeleted, setBox] = useState(0)

  let checker = null

  const decideIfClose = () => {
    checker = setTimeout(()=>{
      inBox === true ? setShow(true) : setShow(false); console.log(inBox)
    }, 500)
  }


  return (
    <div className="App" onMouseMove={({ clientX: x, clientY: y }) => set({ xy: calc(x, y) })}>
      <Header/>
      <header className="App-header">
        <Particles src={logo} width='50vw' height='50vh' />
        <animated.div onMouseEnter={()=>{setInBox(true); clearTimeout(checker); console.log('entered')}} onMouseLeave={()=>{console.log(inBox); setInBox(false); setShow(false)}} class={ showCenter === true ? "center-box-anim" : "center-box-hidden"} style={{ transform: props.xy.interpolate(trans9) }}>
          <CenterBox boxSelected={boxSeleted}/>
        </animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(1)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans1) }} ><GitHubIcon style={{width:'100%', height:'100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(2)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans2) }} ><AccountBalanceIcon style={{width:'100%', height:'100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(3)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans3) }} ><CodeIcon style={{width:'100%', height:'100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(4)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans4) }} ><AccountTreeIcon style={{width:'100%', height:'100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(5)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans5) }} ><WorkIcon style={{width:'100%', height:'100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(6)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans6) }} ><EventAvailableIcon style={{width:'100%', height:'100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(7)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans7) }} ><BusinessIcon style={{width:'100%', height: '100%'}}/></animated.div>
        <animated.div class="move-card" onMouseEnter={()=>{clearTimeout(checker); setShow(true); setBox(8)}} onMouseLeave={()=>{decideIfClose()}} style={{ transform: props.xy.interpolate(trans8) }} ><PersonIcon style={{width:'100%', height:'100%'}} /></animated.div>
      </header>
    </div>
  );
}

export default App;
