import React from 'react'
import { Box, Grid, Typography, Button } from '@material-ui/core'

import {GameStart} from '../functions/gameFunctions'

//Styles
var GameButton = {
    minWidth: '300px',
    minHeight:'95px'
}

export default function Header() {
    return (
        <Box id='header'>
            <Grid container style={{width: '100%', height: '100%', justifyContent:'space-between'}} alignContent='end'>
                <Box color='white' item width='100%' justifyContent='center' alignItems='center' display='flex' textAlign='start' flexDirection='column'>
                    <Typography variant='h4'>Fallen Angel (Devon Owen)</Typography>
                    <Typography>Full Stack Web Developer</Typography>
                </Box>
                {/* <Box width='40%' height='100%' display='flex' justifyContent='flex-end'>
                    <Button onClick={()=>{GameStart()}} variant="contained" color="primary" height='100%' style={GameButton}>Game</Button>
                </Box> */}
            </Grid>
        </Box>
    )
}
