import RoshiPicture from '../Images/RoshiHealth.png'
import RanchersBest from '../Images/RanchersBest.png'
import NameplatePicture from '../Images/discordpfp.png'


import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import EmailIcon from '@material-ui/icons/Email';

const pages = [
    ({ style }) => <animated.div style={{ ...style}}>
        <Box height='100%' width='100%' style={{display: 'flex', flexDirection: 'column', alignItems:'center'}}>
            <Box height='50%' width='90%' style={{backgroundImage: `url(${RoshiPicture})`, backgroundSize:'contain'}}></Box>
            <Box class='startupBoxHeader'><Typography variant='h4'>Roshi Health</Typography></Box>
            <Box class='startupBoxInfo'><Typography variant='h5'>Roshi Health was a telehealth platform focusing on preventative health and creating a health plan. I built a CMS and a web APP for this startup.</Typography></Box>
        </Box>
        </animated.div>,
    ({ style }) => <animated.div style={{ ...style}}>
        <Box height='100%' width='100%' style={{display: 'flex', flexDirection: 'column', alignItems:'center'}}>
            <Box height='50%' width='90%' style={{backgroundImage: `url(${RanchersBest})`, backgroundSize:'contain'}}></Box>
            <Box class='startupBoxHeader'><Typography variant='h4'>Ranchers Best</Typography></Box>
            <Box class='startupBoxInfo'><Typography variant='h5'>Ranchers Best is a meat box startup that brings meat directly from the farms to the table. Its currently looking for an investor. I handle the website and mobile APP for this startup. </Typography></Box>
        </Box>
    </animated.div>
  ]

export default function CenterBox(props) {

// Startup Boxes Anims ------------------------------------------------------------------------------------------------------------------------------------------------------------------------
    const [index, set] = useState(0)
    const onClick = useCallback(() => set(state => (state + 1) % 2), [])
    const transitions = useTransition(index, p => p, {
      from: { opacity: 0, transform: 'translate3d(100%,0,0)' },
      enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
      leave: { opacity: 0, transform: 'translate3d(-50%,0,0)' },
    })

// ------------------------------------------------------------------------------------------------------------------------------------------------------------------------
    return (
        props.boxSelected === 1 ? <Box1/> :
        props.boxSelected === 2 ? <Box2/> :
        props.boxSelected === 3 ? <Box3/> :
        props.boxSelected === 4 ? <Box4/> :
        props.boxSelected === 5 ? <Box5/> :
        props.boxSelected === 6 ? <Box6/> :
        props.boxSelected === 7 ? <Box7 onClick={onClick} transitions={transitions}/> :
        props.boxSelected === 8 ? <Box8/> :
        null
    )
}


